const listOfImages = document.querySelector(".gallery");


function imageTemplate({original, preview, description}) {
  return `
    <li class="gallery-item">
  <a class="gallery-link" href="${original}">
    <img
      width="360"
      height="200"
      class="gallery-image"
      src="${preview}"
      alt="${description}"
    />
  </a>
</li>

  `;
}

function imagesTemplate(array) {
  return array.map(imageTemplate).join("");
}

function render(array) {
  const markup = imagesTemplate(array);
  listOfImages.innerHTML = markup;
}

render(images);

new SimpleLightbox('.gallery a',{
  captions: true,
  captionsData:	'alt',
  captionsDelay: 250,
});
